iframe {
  display: none;
}

html {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 24px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  margin: 0;

  background: linear-gradient(#e66465, #9198e5);
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 320px;
  height: 150px;
  padding: 10px;
  border: 5px solid rgb(32, 32, 32);
  border-radius: 10px;
  background-color: azure;
}
